import { useLogoutMutation } from "@/state/slices/authApiSlice";
import { useGetInvoicesMutation } from "@/state/slices/invoicesApiSlice";
import Invoice from "@/types/Invoice";
import InvoiceTable from "@/pages/dashboard/invoices/(invoicesList)/components/invoiceTable/InvoiceTable";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useInvoicesListState } from "../layout";

export default function Deliveries() {
  const [invoices, setInvoices] = useState<Invoice[]>([]);
  const { invoices: parentInvoices, isLoading } = useInvoicesListState();

  useEffect(() => {
    if (parentInvoices) setInvoices(parentInvoices);
  }, [parentInvoices]);

  return (
    <>
      <div className="">
        <InvoiceTable
          invoices={invoices}
          setInvoices={setInvoices}
          showCheckbox={true}
          loading={isLoading}
        />
      </div>
    </>
  );
}
