import {
  Navigate,
  RouterProvider,
  createBrowserRouter,
} from "react-router-dom";
import PrivateRoute from "./components/privateRoute";
import Dashboard from "./pages/dashboard";
import Login from "./pages/(auth)/login";
import Forgot from "./pages/(auth)/forgot-password";
import Reset from "./pages/(auth)/reset-password";
import LogoLayout from "./components/forms/LogoLayout/LogoLayout";
import Deliveries from "@/pages/dashboard/invoices/(invoicesList)/deliveries";
import Collections from "@/pages/dashboard/invoices/(invoicesList)/collections";
import Completed from "@/pages/dashboard/invoices/(invoicesList)/completed";
import InvoicesList from "./pages/dashboard/invoices/(invoicesList)/layout";
import Details from "@/pages/dashboard/invoices/details";
import PublicRoute from "./components/publicRoute";

let router = createBrowserRouter(
  [
    {
      path: "",
      element: <Navigate to="/login" />,
    },
    {
      path: "",
      element: (
        <PublicRoute>
          <LogoLayout />
        </PublicRoute>
      ),
      children: [
        {
          path: "login",
          element: <Login />,
        },
        {
          path: "forgot-password",
          element: <Forgot />,
        },
        {
          path: "reset-password",
          element: <Reset />,
        },
      ],
    },
    {
      path: "dashboard",
      element: (
        <PrivateRoute>
          <Dashboard />
        </PrivateRoute>
      ),
      children: [
        {
          path: "invoices",
          // element: <Invoice />,
          children: [
            {
              path: "",
              element: <InvoicesList />,
              children: [
                {
                  path: "",
                  element: <Navigate to="/dashboard/invoices/deliveries" />,
                },
                {
                  path: "deliveries",
                  element: <Deliveries />,
                },
                {
                  path: "collections",
                  element: <Collections />,
                },
                {
                  path: "completed",
                  element: <Completed />,
                },
              ],
            },
            {
              path: ":invoiceNo",
              element: <Details />,
            },
          ],
        },
      ],
    },
  ]
  // {
  // basename: "/indeliv2/public",
  // }
);

export default function MyRouter() {
  return <RouterProvider router={router} />;
}
