import "./invoice-table.scss";
import { useState, useEffect, useMemo, FormHTMLAttributes } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useInvoicesListState } from "../../layout";
import Invoice, {
  InvoiceHead,
  getKeyFromHead,
  invoiceHeadings,
  invoiceKeyHeadMap,
} from "@/types/Invoice";
import LoadingTd from "@/components/LoadingTd";
import { SortingDir } from "@/utils/enums";
import InvoiceTableTh from "../InvoiceTableTh";
import { formatDate, numberWithCommas } from "@/utils/helper";

export type SortingState = Record<
  InvoiceHead["head"],
  {
    dir: SortingDir;
    optionsOpen: boolean;
  }
>;

export default function InvoiceTable({
  invoices,
  showCheckbox,
  setInvoices,
  loading = false,
  excludedHeadings = ["Due", "Overdue By"],
}: {
  invoices: Invoice[];
  showCheckbox?: boolean;
  setInvoices: React.Dispatch<React.SetStateAction<Invoice[]>>;
  loading?: boolean;
  excludedHeadings?: InvoiceHead["head"][];
}) {
  // state for saving current state of invoices after sorting
  const [sortedInvoices, setSortedInvoices] = useState(invoices);
  const navigate = useNavigate();

  useEffect(() => {
    setSelectedCount(invoices.filter((i) => i.checked).length);
    if (sortedInvoices.length === 0) {
      setSortedInvoices(invoices);
    }
  }, [invoices]);

  // State for saving sorting states of table headigns
  const [sortingStates, setSortingStates] = useState<SortingState>({});

  const [sortStack, setSortStack] = useState<InvoiceHead["head"][]>([]);
  const moveToTop = (value: InvoiceHead["head"]) => {
    setSortStack((prevStack) => {
      const filteredStack = prevStack.filter((item) => item !== value);
      return [value, ...filteredStack];
    });
  };

  // Getting state coming from parent <Outlet />
  const { setSelectedCount, searchTxt } = useInvoicesListState();

  // Creating headings from invoiceHeadings excluding excludedHeadings
  const headings = useMemo(() => {
    return invoiceHeadings.filter(
      (heading) =>
        !excludedHeadings.includes(heading.head) && heading.head !== ""
    );
  }, [excludedHeadings]);

  // Initializing Sorting States such that all headings are asc and options closed
  useEffect(() => {
    if (headings.length && !Object.keys(sortingStates).length) {
      const newSortingStates: typeof sortingStates = {};
      const newSortStack: typeof sortStack = [];
      headings.forEach((h) => {
        if (h.sortable) {
          newSortingStates[h.head] = {
            dir: SortingDir.ASC,
            optionsOpen: false,
          };
          newSortStack.push(h.head);
        }
      });
      setSortingStates(newSortingStates);
      setSortStack(newSortStack);
    }
  }, [headings]);

  // Sorting Invoices whenever sortingStates changes
  useEffect(() => {
    const sortData = (a: Invoice, b: Invoice): number => {
      for (let i = 0; i < sortStack.length; i++) {
        const prop = getKeyFromHead(sortStack[i]);
        if (prop) {
          const heading = invoiceKeyHeadMap[prop].head;
          const aVal = a[prop] || "";
          const bVal = b[prop] || "";
          const asc = sortingStates[heading]?.dir === SortingDir.ASC;
          if (heading !== "" && sortingStates[heading]) {
            if (aVal > bVal) return asc ? 1 : -1;
            else if (aVal < bVal) return asc ? -1 : 1;
          }
        }
      }
      return 0;
    };
    setSortedInvoices((s) => [...s].sort(sortData));
  }, [sortingStates]);

  const [selectAllChecked, setSelectAllChecked] = useState(false);
  const handleSelectAllCheckbox = (e: React.ChangeEvent<HTMLInputElement>) => {
    const checked = e.target.checked;
    setSelectAllChecked(checked);
    const updatedInvoices = invoices.map((invoice) => ({
      ...invoice,
      checked: checked,
    }));
    const updatedSortedInvoices = sortedInvoices.map((invoice) => ({
      ...invoice,
      checked: checked,
    }));

    setInvoices(updatedInvoices);
    setSortedInvoices(updatedSortedInvoices);
  };

  // handing checkbox changed action to update current invoices and parent ones
  const handleCheckboxChange = (invoice_number: string) => {
    const updateInvoicesChecked = (oldInvoices: Invoice[]) => {
      const newInvoices = [...oldInvoices];
      const fIndex = oldInvoices.findIndex(
        (i) => i.invoice_number === invoice_number
      );
      const curInvoice = { ...newInvoices[fIndex] };
      if (curInvoice.checked && selectAllChecked) {
        setSelectAllChecked(false);
      }
      newInvoices.splice(fIndex, 1, {
        ...curInvoice,
        checked: !curInvoice.checked,
      });

      return newInvoices;
    };

    setInvoices(updateInvoicesChecked);
    setSortedInvoices(updateInvoicesChecked);
  };

  const searchCond = (i: Invoice): boolean =>
    i.invoice_number.includes(searchTxt) || i.buyer_name.includes(searchTxt);

  const detailLink = (invoice: Invoice): string => {
    return showCheckbox ? `/dashboard/invoices/${invoice.invoice_number}` : "";
  };

  return (
    <>
      <table className="invoice-table-component">
        <thead>
          <tr>
            {showCheckbox && (
              <th>
                <input
                  placeholder="test"
                  type="checkbox"
                  className="checkbox"
                  checked={selectAllChecked}
                  onChange={handleSelectAllCheckbox}
                />
              </th>
            )}

            {headings.map((heading) => (
              <InvoiceTableTh
                key={heading.head}
                heading={heading}
                sortingStates={sortingStates}
                setSortingStates={setSortingStates}
                moveToTop={moveToTop}
              />
            ))}
          </tr>
        </thead>
        <tbody>
          {loading ? (
            <tr className="loading-row">
              <LoadingTd cols={headings.length + (showCheckbox ? 1 : 0)} />
            </tr>
          ) : (
            sortedInvoices.filter(searchCond).map((invoice, index) => (
              <tr
                key={index}
                className={`${invoice.checked && "active"}  ${
                  !showCheckbox && "no-checkbox"
                }`}
              >
                {showCheckbox && (
                  <td className="check">
                    <label htmlFor={invoice.invoice_number}>
                      <input
                        type="checkbox"
                        checked={invoice.checked}
                        id={invoice.invoice_number}
                        onChange={() =>
                          handleCheckboxChange(invoice.invoice_number)
                        }
                      />
                    </label>
                  </td>
                )}
                <div
                  className="link-wrapper"
                  onClick={() => navigate(detailLink(invoice))}
                >
                  <td className={`invoice-no`}>
                    <span>{invoice.invoice_number}</span>
                    {showCheckbox && (
                      <Link to={detailLink(invoice)} className="view-detail">
                        View Detail
                      </Link>
                    )}
                  </td>
                  <td className={`date other ${!invoice.overdueBy && "last"}`}>
                    {formatDate(invoice.invoice_date)}
                  </td>
                  <td className="buyer">{invoice.buyer_name}</td>
                  <td className="company other">{invoice.tally_company}</td>
                  <td className="amount">
                    ₹{numberWithCommas(invoice.invoice_amount)}
                  </td>
                  {invoice.due && <td className="due other">{invoice.due}</td>}
                  {invoice.overdueBy && (
                    <td className="overdue other last">{invoice.overdueBy}</td>
                  )}
                </div>
              </tr>
            ))
          )}
        </tbody>
      </table>
    </>
  );
}
